body {
	margin: 0;
	box-sizing: border-box;
}

p {
	margin: 0;
}

.title {
	text-align: center;
}

.wrapper {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 100%;
}

.content-wrapper {
	display: flex;
	justify-content: space-between;
	width: 45%;
	padding: 40px;
  border: 5px solid #000;
	margin: auto;
}

.drum-container {
	display: grid;
	width: calc(50% - 40px);
  grid-template-columns: 33% 33% 33%;
  grid-gap: 20px;
  margin-right: 40px;
}

.drum-pad {
	display: flex;
	align-items: center;
	justify-content: center;
  background-color: #ddd;
  border: 1px solid black;
  text-align: center;
  box-shadow: 5px 10px 8px #888;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.5rem;
  font-weight: 600;
}

.drum-pad:hover {
	opacity: .7;
}

.drum-pad .keypad {
	user-select: none;
}

.option {
	display: flex;
	flex-direction: column;
	align-items: center;
	width: calc(50% - 40px);
}

.switch-wrapper {
	margin: 10px 0 20px 0;
}

.subtitle {
	display: block;
	font-weight: bold;
	font-size: 1.25rem;
	text-align: center;
	user-select: none;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 30px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 22px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.display-container {
	background-color: #eee;
	width: 100%;
	padding: 15px 0;
	text-align: center;
	font-size: 1.5rem;
	border: 1px solid #000;
	border-radius: 2px;
	margin: 40px 0;
}

.slide-container {
	width: 100%;
	display: flex;
	justify-content: center;
}

.slide {
	width: 75%;
}

@media (max-width: 1024px) {
  .content-wrapper {
    width: 50%;
  }
}

@media (max-width: 690px) {
  .content-wrapper {
    width: 70%;
  }
}

@media (max-width: 500px) {
  .content-wrapper {
    width: 85%;
    padding: 20px;
  }

  .switch-wrapper {
  	margin: 10px 0;
  }

  .display-container {
  	margin: 10px 0;
  }
}
